body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: #d2b48c;
}

h1 {
  color: #8b4513;
  font-size: 50px;
  font-family: "Monotype Corsiva";
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.aoiro {
  color: blue;
}

.e {
  position: absolute;
  top: 30%;
  left: 35%;
  border: 1px solid;
}

.osusume {
  font-weight: bold;
}

.goukei {
  color: red;
  font-weight: bold;
}
